  --Orange: hsl(26, 100%, 55%);
  --Pale-orange: hsl(25, 100%, 94%);
  --Very-dark-blue: hsl(220, 13%, 13%);
  --Dark-grayish-blue: hsl(219, 9%, 45%);
  --Grayish-blue: hsl(220, 14%, 75%);
  --Light-grayish-blue: hsl(223, 64%, 98%);
  --White: hsl(0, 0%, 100%);
  --Black: hsl(0, 0%, 0%);
  --Black-bg: hsl(0, 0%, 0%, 75%);
  --fontsize: 16px;
  --system-ui: system-ui, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

body {
  font-family: "Kumbh Sans", var(--system-ui);
}

.removeButton {
  background: center no-repeat url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  width: 25px;
  height: 25px;
  border: none;
}

.removeButton:hover {
  border: 1px solid black;
}

p,
h2 {
  margin: 0;
}

.header {
  display: flex;
  align-items: center;
  justify-content: space-around;
  margin-bottom: 100px;
  height: 100px;
}

.headerLogo,
.headerAction,
.headerLink,
.headerLink > div,
.containerPriceText,
.actionCartContainer,
.amountCartText,
.bodyModal {
  display: flex;
  align-items: center;
}

#headerCartProfile {
  width: 55px;
  height: 55px;
  display: flex;
  justify-content: center;
  align-items: center;
  border: none;
  border-radius: 50%;
}

#headerCartProfile:hover {
  border: 2px solid var(--Orange);
}

#headerCartProfile > img {
  height: 50px;
  width: 50px;
}

#headerCartBtn {
  border: none;
  background: none;
  margin-right: 50px;
}

#iconCart > path:hover {
  fill: black;
}

.headerLink {
  gap: 25px;
  margin-left: 75px;
}

#headerCartAmount {
  position: relative;
  right: 65px;
  bottom: 18px;
  border-radius: 25px;
  padding: 0 5px;
}

.headerLink > div {
  height: 55px;
}

a {
  text-decoration: none;
  color: var(--Dark-grayish-blue);
}

a:hover {
  color: var(--Very-dark-blue);
}

.headerLink > div:hover {
  border-bottom: 1px solid var(--Orange);
}

.body,
.footerModal,
.amountCartContainer {
  display: flex;
}

.body {
  width: 100vw;
}

.productImageContainer {
  width: 50%;
}

.productActionContainer {
  width: 30%;
}

.largeProductImage {
  display: flex;
  justify-content: center;
}

#largeImage,
.thumbnailImage {
  border-radius: 25px;
}

#largeImage {
  width: 500px;
}

.thumbnailImage {
  width: 100px;
}

.thumbnailImage:hover {
  filter: brightness(0.5);
}

.addToCartBtn:hover,
#checkoutButton:hover {
  background-color: var(--Pale-orange);
}

.incrementAction use:hover,
.decrementAction use:hover {
  fill: var(--Pale-orange);
}

.thumbnailContainer,
.incrementAction,
.decrementAction,
.amountCartText,
.addToCartBtn,
.dialogWrapper,
.previousBtn,
.nextBtn,
.itemContainer,
.productImage,
.checkoutContainer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.thumbnailContainer {
  gap: 25px;
  margin-top: 30px;
}

.companyText {
  margin: 50px 0 30px;
}

.companyText,
.productDescText,
.originalPriceText {
  color: var(--Dark-grayish-blue);
}

.productNameText {
  font-size: 50px;
  margin-bottom: 40px;
}

.productDescText {
  margin-bottom: 30px;
}

.containerPriceText {
  gap: 30px;
  margin-bottom: 20px;
}

.productPriceText {
  font-weight: bolder;
  font-size: 30px;
}

.containerDiscountText {
  padding: 3px 10px;
  background-color: var(--Black);
  color: white;
  font-weight: bold;
  border-radius: 5px;
}

.originalPriceText {
  margin-bottom: 60px;
  font-weight: bold;
}

.amountCartContainer {
  width: 38%;
  margin-right: 20px;
  background-color: var(--Light-grayish-blue);
}

.checkoutContainer {
  width: 60%;
}

.incrementAction,
.decrementAction {
  width: 40%;
  height: 40px;
  border: none;
  background-color: transparent;
}

.amountCartText {
  width: 30px;
  text-align: center;
}

.addToCartBtn {
  width: 100%;
  height: 40px;
  border: none;
  background-color: var(--Orange);
  gap: 10px;
  color: black;
  font-weight: bold;
  font-size: 15px;
  border-radius: 5px;
}

.addToCartBtn > svg > path {
  fill: black;
}

.dialogContainer {
  border: none;
  width: 100vw;
  height: 100vh;
  background-color: rgb(0, 0, 0, 0.6);
}

.dialogWrapper {
  margin-top: 5%;
}

.cancelContainer {
  display: flex;
  justify-content: end;
  margin-right: 40px;
  margin-bottom: 30px;
}

.cancelBtn {
  background-color: transparent;
  border: none;
}

.cancelBtn > svg > path {
  fill: white;
}

.previousBtn,
.nextBtn {
  position: relative;
  border-radius: 50%;
  height: 50px;
  width: 50px;
  border: none;
}

.nextBtn {
  right: 27px;
}

.previousBtn {
  left: 25px;
}

.cancelBtn path:hover {
  fill: var(--Orange);
}

.nextBtn path:hover,
.previousBtn path:hover {
  stroke: var(--Orange);
}

.cart {
  position: absolute;
  top: 100px;
  box-shadow: 0px 3px 10px 5px rgb(224, 224, 224);
}

.cartHeader {
  padding: 15px 30px;
  border-bottom: 1px solid var(--Grayish-blue);
  display: flex;
  justify-content: space-between;
}

#cartExitBtn {
  border: none;
  background-color: red;
  border-radius: 5px;
  color: white;
  font-weight: bold;
}

#cartExitBtn:hover {
  background-color: rgb(245, 135, 135);
}

.productImage {
  width: 40px;
  border-radius: 5px;
}

.itemContainer {
  padding: 20px 30px;
  gap: 20px;
}

.checkoutContainer {
  width: 100%;
}

#checkoutButton {
  width: 325px;
  height: 45px;
  margin: 10px 0px 30px;
  background-color: var(--Orange);
  border: none;
  border-radius: 5px;
  font-weight: bold;
}
